<template>
	<view v-if="list.length&&tabSwiper.length">
		<u-tabs-swiper ref="uTabs" :list="tabSwiper" :current="current" @change="tabsChange" :is-scroll="false" swiperWidth="750" active-color="#e770a4"></u-tabs-swiper>
		<scroll-view scroll-y class="scroll" :scroll-top="scrollTop" @scroll="scrollChange">
			<swiper :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish" style="height:100vh;">
				<swiper-item v-for="(item,index) in list" :key="index">
					<block v-for="(jtem,jndex) in item.children" :key="jndex">
						<free-form :item="jtem" :placeholderText="placeholderText"></free-form>
					</block>
				</swiper-item>
			</swiper>
		</scroll-view>

	</view>
</template>

<script>
	export default {
		props: {
			scrollTop: {
				type: [Number, String],
				default: 0
			},
			tabSwiper: {
				type: Array,
				default: () => {
					return []
				}
			},
			list: {
				type: Array,
				default: () => {
					return []
				}
			}
		},
		data() {
			return {
				placeholderText: '妻子',
				btnTextStatus: '下一页',
				current: 0,
				swiperCurrent: 0,
				customBtnStyle: {
					width: '600rpx',
					backgroundColor: '#e770a4',
					color: '#fff',
					height: '90rpx',
					border: '1rpx solid #e770a4'
				}
			}
		},
		mounted() {
			
		},
		watch: {
			current: {
				handler(newVal) {
					this.$emit('updateData', this.list, newVal);
					if (newVal == 0) {
						this.placeholderText = '妻子';
					} else {
						this.placeholderText = '丈夫';
					}
					return newVal == this.list.length - 1 ? this.btnTextStatus = '提交' : this.btnTextStatus = '下一页';
				},
				deep: true
			}
		},
		methods: {
			tabsChange(index) {
				this.swiperCurrent = index;
			},
			// swiper-item左右移动，通知tabs的滑块跟随移动
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			// 由于swiper的内部机制问题，快速切换swiper不会触发dx的连续变化，需要在结束时重置状态
			// swiper滑动结束，分别设置tabs和swiper的状态
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
			handleSubmitOrNextBtn() {
				if (this.btnTextStatus == '下一页') {
					this.current++;
					this.swiperCurrent = this.current;
				} else {
					this.$emit('submit',this.list);
				}
			},
			scrollChange(e) {
				this.$emit('scroll', e)
			}
		}
	}
</script>

<style scoped>
	.scroll {
		height: calc(100vh - 180rpx);
	}

	.action-btn {
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 100rpx;
		display: flex;
		align-items: center;
		justify-content: center;
	}
</style>
